.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: var(--body-color);
}

.nav {
  height: calc(var(--header-height) + 1.5rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
}

.nav-logo, .nav-toggle {
    color: var(--title-color);
    font-weight: var(--font-medium);
}

.nav-list {
  display: flex;
  grid-column-gap: 2rem;
  -webkit-column-gap: 2rem;
          column-gap: 2rem;
}

.nav-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: var(--title-color);
  font-weight: var(--font-medium);
  transition: .3s;
}

.nav-icon, .nav-close, .nav-toggle {
    display: none;
}

.active-link {
    color: var(--title-color-dark);
    text-decoration: underline;

}

.nav-link:hover {
  color: var(--text-color-hover);
}

.scroll-header {
  box-shadow: 0 -1px 4px #00000026;
}

/* medium */
@media screen and (max-width: 768px) {
  .header {
    top: initial;
    bottom: 0;
  }

  .nav {
    height: var(--header-height);
  }

  .nav-menu {
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color: var(--body-color);
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
    border-radius: 1.5rem 1.5rem 0 0;
    transition: .3s;
  }

  .show-menu {
    bottom: 0;
  }

  .nav-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
  }

  .nav-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
  }

  .nav-icon {
    font-size: 1.2rem;
  }

  .nav-close {
    position: absolute;
    right: 1.3rem;
    bottom: .5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--title-color);
  }

  .nav-close:hover {
    color: var(--title-color-dark);
  }

  .nav-toggle {
    font-size: 1.1rem;
    cursor: pointer;
  }

  .nav-icon, .nav-close, .nav-toggle {
      display: block;
  }
}

@media screen and (max-width: 350px) {
  .nav-menu {
    padding: 2rem 0.25rem 4rem;
  }

  .nav-list {
    grid-column-gap: 0;
    -webkit-column-gap: 0;
            column-gap: 0;
  }
}

