.icon-wrapper {
  display: inline-flex;
  list-style: none;
  margin-left: 22px;
}

.icon-wrapper .icon {
  color:#969696;
  position: relative;
  background: #ffffff;
  border-radius: 50%;
  padding: 15px;
  margin: 12px;
  width: 40px;
  height: 40px;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: all 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.icon-wrapper .tooltip {
  position: absolute;
  top: 0;
  font-size: 14px;
  background: #ffffff;
  color: #ffffff;
  padding: 5px 8px;
  border-radius: 5px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.icon-wrapper .tooltip::before {
  position: absolute;
  content: "";
  height: 8px;
  width: 8px;
  background: #ffffff;
  bottom: -3px;
  left: 50%;
  -webkit-transform: translate(-50%) rotate(45deg);
          transform: translate(-50%) rotate(45deg);
  transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.icon-wrapper .icon:hover .tooltip {
  top: -45px;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.icon-wrapper .icon:hover span,
.icon-wrapper .icon:hover .tooltip {
  text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.1);
}

.icon-wrapper .linkedin:hover,
.icon-wrapper .linkedin:hover .tooltip,
.icon-wrapper .linkedin:hover .tooltip::before {
  background: #1877F2;
  color: #ffffff;
}

.icon-wrapper .github:hover,
.icon-wrapper .github:hover .tooltip,
.icon-wrapper .github:hover .tooltip::before {
  background: #333333;
  color: #ffffff;
}

.icon-wrapper .instagram:hover,
.icon-wrapper .instagram:hover .tooltip,
.icon-wrapper .instagram:hover .tooltip::before {
  background: #E4405F;
  color: #ffffff;
}

